body {
  box-sizing: border-box;
  background-image: url("bg_cat.73f6b984.jpg");
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

img {
  width: 40%;
  height: 40%;
  margin-top: 10px;
  display: block;
}

.cat-info {
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.cat-info p {
  width: 600px;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  display: flex;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

